export function applyAlter(e: PointerEvent) {
  e.stopImmediatePropagation();
  e.stopPropagation();
  e.preventDefault();

  console.log("applyAlter: ", e.target);

  const el = e.target as HTMLElement;

  const parent = el.closest("[data-parent]") as HTMLElement;

  const isActive = parent.dataset.active ? true : false;
  if (isActive && el?.dataset?.ignore === "active") return;
  applyDeepAlter(parent, isActive);
  parent.dataset.active = isActive ? "" : "true";
  return isActive;
}

export function applyAlterForced(e: PointerEvent, isActive: boolean = false) {
  e.stopImmediatePropagation();
  e.stopPropagation();
  e.preventDefault();

  const parent = (e.target as HTMLElement).closest(
    "[data-parent]",
  ) as HTMLElement;

  if (isActive && (e?.target as HTMLElement)?.dataset?.ignore === "active")
    return;
  applyDeepAlter(parent, isActive);
  parent.dataset.active = isActive ? "" : "true";
}

export function applyDeepAlter(el: HTMLElement, isActive: boolean = false) {
  const alter = el?.dataset?.alter;

  if (alter) {
    const [enabledClasses, disableClasses] = alter
      .split(";")
      .map((x) => x.trim().split(" "));

    if (isActive) {
      el.classList.remove(...disableClasses);
      el.classList.add(...enabledClasses);
    } else {
      el.classList.remove(...enabledClasses);
      el.classList.add(...disableClasses);
    }
  }

  const els: NodeListOf<HTMLElement> = el.querySelectorAll(
    "[data-alter]",
  ) as NodeListOf<HTMLElement>;

  els.forEach((el: HTMLElement) => applyDeepAlter(el, isActive));
}
