import { getUserListItems } from "./../util/db.js";
import { getItems } from "./idb.js";
import { getDB } from "./idb.js";


export function getWriteryListItems(user: User) {
	try {
		//@ts-ignore
		return getUserListItems(user);

	} catch (e) {
		console.warn(e);
		return getItems("writings");
	}
}

export function saveWriteryListItem(hubble) {
	console.log("saveWriteryListItem: ", hubble);
	getDB().then((d) => d.put("writery", hubble));
}


function stringArrayToArray(stringArr) {
	console.log("stringArrayToArray", stringArr);
	return stringArr
		.replace("[", "")
		.replace("]", "")
		.replace(/"/g, "")
		.trim()
		.split(",")
		.filter((x) => x)
		.map((x) => x.trim().replace(/\n/g, ""));
}
